import {ajax,showLoading,hideLoading} from '@/ajax.js'
export default {
	data() {
		return {
			list:[],
			userinfo:'',
			canIUse: uni.canIUse('button.open-type.getUserInfo')
		}
	},
	onLoad() {
		let _this = this
		uni.setNavigationBarTitle({
			title: '首页'
		});

		uni.login({
		  provider: 'weixin',
		  success: function (loginRes) {
			uni.getUserInfo({
			  provider: 'weixin',
			  success: function (infoRes) {
				  _this.userinfo = infoRes.userInfo
			  }
			});
		  }
		});

	},
	onShow() {
		let _this = this
		let login = uni.getStorageSync('login')
		if(login==''||login==null||login==undefined){

			uni.login({  
				success: function(res) { 
					uni.request({
						url: ajax.wxlogin, //仅为示例，并非真实接口地址。
						header:{
							'Content-Type':'application/x-www-form-urlencoded'
						},
						data:{
							code:res.code
						},
						method:'POST',
						success: (res) => {
							if(res.data.code==200){
								let timeData = (new Date()).getTime() + 1000 * 60 * 60 * 2;
								uni.setStorageSync('login', res.data.data.token);
								uni.setStorageSync('time', timeData);
								_this.info()
							}
						}
					});
				}  
			});






		}else{
			_this.info()
		}
	},
	methods: {
		sedbarBtn(){









		},
		
		bindGetUserInfo(e){
			this.userinfo = e.detail.userInfo;
			if(this.userinfo!=''){
				uni.navigateTo({
					url: '/pages/index/new_article'
				})
			}
			console.log('1111',e)
		},
		info(){
			let _this = this;
			let login = uni.getStorageSync('login')
			if(login!=''&&login!=null&&login!=undefined){
				showLoading()
				uni.request({
					url: ajax.allperday, //仅为示例，并非真实接口地址。
					header:{
						Authorization:'bearer' +' '+ login
					},
					data:{},
					success: (res) => {
						if(res.data.code==200){
							hideLoading()
							_this.list = res.data.data
						}
					}
				});
			}
		},
		itemBtn(id){
			uni.navigateTo({
				url: '/pages/index/article_details?id='+id
			})
		}
	}
}
